<template>
  <v-container class="pt-10">
    <v-row>
      <v-col>
        <v-btn prepend-icon="mdi-calendar-check" @click="this.currentView = 'reservation'" :disabled="this.currentView === 'reservation'">診察予約</v-btn>
      </v-col>
      <v-col>
        <v-btn prepend-icon="mdi-calendar-question-outline" @click="this.currentView = 'clientInfo'" :disabled="this.currentView === 'clientInfo'">予約状況</v-btn>
      </v-col>
      <v-col>
        <v-btn prepend-icon="mdi-history" @click="this.currentView = 'history'" :disabled="this.currentView === 'history'">診察履歴</v-btn>
      </v-col>
      <v-col>
        <v-btn prepend-icon="mdi-account-outline" @click="this.currentView = 'account'" :disabled="this.currentView === 'account'">ユーザ登録情報</v-btn>
      </v-col>
    </v-row>
    <v-divider class="mt-2" thickness="2"></v-divider>
  </v-container>
  <v-container class="pt-10">
    <v-slide-x-transition mode="out-in">
      <v-row v-if="currentView === 'clientInfo'" key="clientInfo">
        <ClientInfo/>
      </v-row>
      <v-row v-else-if="currentView === 'reservation'" key="reservation">
        <Reservation :current-view="currentView" @switch-view="switchView"/>
      </v-row>
      <v-row v-else-if="currentView === 'history'" key="history">
        <ExamHistory/>
      </v-row>
      <v-row v-else-if="currentView === 'account'" key="account">
        <UserAccount/>
      </v-row>
    </v-slide-x-transition>
  </v-container>
</template>

<script>
import ClientInfo from '../components/client_page/ClientInfo.vue';
import Reservation from '../components/client_page/Reservation.vue';
import ExamHistory from '../components/client_page/ExamHistory.vue';
import UserAccount from '../components/client_page/UserAccount.vue';

export default {
  name: "ClientTop",
  components: {
    ClientInfo,
    Reservation,
    ExamHistory,
    UserAccount,
  },
  data() {
    return {
      currentView: 'reservation',
    }
  },
  methods: {
    switchView(view) {
      this.currentView = view;
    },
  }
};
</script>

